import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { operationChangeRecordComponent } from './operationChangeRecord.component';
import { RecordQueryComponent } from './record-query/record-query.component';
import { PostChangeComponent } from './post-change/post-change.component';
export const routes: Routes = [
	{
		path: '',
		component: operationChangeRecordComponent,
		children: [
			{ path: '', redirectTo: 'post-change', pathMatch: 'full' },
			{
				path: 'post-change',
				component: PostChangeComponent
			},
			{
				path: 'record-query',
				component: RecordQueryComponent,
			},
		]
	},
]

export const operationChangeRecordRouting: ModuleWithProviders = RouterModule.forChild(routes);
