import { Component, OnInit } from '@angular/core';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Message, SelectItem } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { CommonHttpService } from '../../../http/common.http.service';

@Component({
  selector: 'recordQueryComponent',
  templateUrl: './record-query.component.html',
  styleUrls: ['./record-query.component.scss'],
  providers: [CustomeHttpService]
})
export class RecordQueryComponent implements OnInit {

  operateTypeList: any;
  toOperateRecode: any;
  fromOperateRecode: any;
  index: any;
  code: any;
  msgs: Message[] = [];//提示信息
  tableData: Array<any> = [];
  first: number = 0;//分页控制
  pageSize: string;
  pageNum: string;
  total: number;
  elementDisplay: string;
  ch: any = CALENDAR_CN;//讲英文转化为中文
  externalIdType: string = 'PERMISSION';//操作类型
  applyByUserLogin: string;//申请人
  applyByOrgId: string;//申请人机构
  dateFrom: Date = null;//申请开始日期
  dateEnd: Date = null;//申请结束日期
  oldPost: string;//原岗位
  newPost: string;//现岗位
  oldPermission: string;//原权限
  newPermission: string;//现权限
  applyByUserLoginName: string;//申请人名称
  externalIdTypes: any[];//类型码值
  Display: boolean;
  userDisplay: boolean;
  orgDisplay: boolean;
  postDisplay: boolean;
  applyByOrgName: string;
  constructor(
    public commonHttpService: CommonHttpService,
    public commfunc: Commfunc
  ) {
    this.codeValues() //调用方法，获取全部码值
    this.externalIdTypes = this.code['externalIdType'];
    this.operateTypeList = this.code['operateTypeList'];//操作类型
  }

  ngOnInit() {
    this.pageSize = '10';
    this.pageNum = '1';
    this.query();
    this.externalIdTypesChange();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret;
  }

  externalIdTypesChange() {
    this.elementDisplay = this.externalIdType;
    this.fromOperateRecode = this.oldPermission ? JSON.stringify([{ oldPermission: this.oldPermission }]) : '';
    this.toOperateRecode = this.newPermission ? JSON.stringify([{ newPermission: this.newPermission }]) : '';
  }
  clickQuery() {
    this.pageNum = '1';
    this.first = 0;
    this.query();
  }
  query() {
    this.externalIdTypesChange();
    let params = {
      externalIdType: this.externalIdType,
      applyByUserLogin: this.applyByUserLogin,
      applyByOrgId: this.applyByOrgId,
      dateFrom: this.commfunc.transDateN(this.dateFrom),
      dateEnd: this.commfunc.transDateN(this.dateEnd),
      fromOperateRecode: this.fromOperateRecode,
      toOperateRecode: this.toOperateRecode,
      pageNum: this.pageNum,
      pageSize: this.pageSize
    }
    this.commonHttpService.queryOperateRecode(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.total = data.result.total;
        if (data.result.operateRecodeList && data.result.operateRecodeList.length > 0) {
          data.result.operateRecodeList.forEach(item => {
            if (item['applyDate']) {
              item['applyDate'] = this.commfunc.transDateN(new Date(item['applyDate']));
            }
            if (item['reviewDate']) {
              item['reviewDate'] = this.commfunc.transDateN(new Date(item['reviewDate']));
            }
          });
          this.tableData = data.result.operateRecodeList;
        } else {
          this.tableData = [];//列表为空时避免一直显示数据加载中
        }
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
    })
  }

  //重置
  reset() {
    this.externalIdType = 'PERMISSION';
    this.applyByUserLogin = '';
    this.applyByOrgId = '';
    this.dateFrom = null;
    this.dateEnd = null;
    this.applyByUserLoginName = '';
    this.applyByOrgName = '';
    this.oldPermission = '';
    this.newPermission = '';
  }

  userClick() {
    this.Display = true;
    this.userDisplay = true;
  }
  //申请人回调
  userValueCall(event) {
    this.Display = false;
    this.userDisplay = false;
    this.applyByUserLoginName = event[0].tellerName;
    this.applyByUserLogin = event[0].tellerId;

  }
  orgClick() {
    this.Display = true;
    this.orgDisplay = true;
  }
  //机构号回调
  orgValueCall(event) {
    this.Display = false;
    this.orgDisplay = false;
    this.applyByOrgId = event[0].orgId;
    this.applyByOrgName = event[0].orgName;

  }
  close() {
    this.userDisplay = false;
    this.orgDisplay = false;
    this.postDisplay = false;
  }
  //分页
  paginate(event) {
    let rows = event.rows;
    //每页显示的条数
    this.pageSize = rows + '';
    //当前页
    let currentPage = event.page + 1 + '';
    this.pageNum = currentPage;
    this.first = event.rows * event.page;

    //调用查询的方法
    this.query();
  }
  //绑定权限id
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }



}
