import { Component, OnInit } from '@angular/core';
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { Message, SelectItem } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { CommonHttpService } from '../../../http/common.http.service';
import { PostChangeBean } from './bean/postchange.bean';

/**
 * 岗位变更
 */
@Component({
	selector: 'post-change',
	templateUrl: './post-change.component.html',
	styleUrls: ['./post-change.component.scss'],
	providers: [CustomeHttpService]
})
export class PostChangeComponent implements OnInit {
	postChangeBean: PostChangeBean = new PostChangeBean()
	index: any;
	code: any;
	msgs: Message[] = [];//提示信息
	tableData: Array<any> = [];
	first: number = 0;//分页控制
	pageSize: string;
	pageNum: string;
	total: number;
	elementDisplay: string;
	ch: any = CALENDAR_CN;//讲英文转化为中文
	startDate: Date = null;//申请开始日期
	endDate: Date = null;//申请结束日期
	Display: boolean;
	userDisplay: boolean;
	orgDisplay: boolean;
	postDisplay: boolean;
	orgIndex: number;
	param: number;
	oldPostList: any = [];
	newPostList: any = [];
	constructor(
		public commonHttpService: CommonHttpService,
		public commfunc: Commfunc
	) {

	}

	ngOnInit() {
		this.postChangeBean.pageSize = 10;
		this.postChangeBean.pageNum = 1;
		this.query();
	}
	clickQuery() {
		this.postChangeBean.pageNum = 1;
		this.postChangeBean.pageSize = 10;
		this.first = 0;
		this.query();
	}
	query() {
		if (this.commfunc.transDateN(this.startDate)) {
			this.postChangeBean.startDate = this.commfunc.transDateN(this.startDate) + ' ' + '00:00:00';
		}
		if (this.commfunc.transDateN(this.endDate)) {
			this.postChangeBean.endDate = this.commfunc.transDateN(this.endDate) + ' ' + '23:59:59';
		}
		this.commonHttpService.queryPostchangeInfo(this.postChangeBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.total = data.total;
				if (data.postChangeInfoList && data.postChangeInfoList.length > 0) {
					data.postChangeInfoList.forEach(item => {
						if (item['oldPostInfo']) {
							item['oldPostList'] = item['oldPostInfo'].split(',');
						}
						if (item['newPostInfo']) {
							item['newPostList'] = item['newPostInfo'].split(',');
						}
					});
					this.tableData = data.postChangeInfoList;

				} else {
					this.tableData = [];//列表为空时避免一直显示数据加载中
				}
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message })
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
		})
	}

	//重置
	reset() {
		this.postChangeBean = new PostChangeBean();
		this.startDate = null;
		this.endDate = null;
		// this.postChangeBean.pageNum = 1;
		// this.postChangeBean.pageSize = 10;
		// this.first = 0;
	}
	userClick(param) {
		this.param = param;
		this.Display = true;
		this.userDisplay = true;
	}
	//申请人回调
	userValueCall(event) {
		this.Display = false;
		this.userDisplay = false;
		if (this.param == 1) {//申请人
			this.postChangeBean.tellerName = event[0].tellerName;
			this.postChangeBean.tellerId = event[0].tellerId;
		} else {//操作人
			this.postChangeBean.operateTellerName = event[0].tellerName;
			this.postChangeBean.operateTellerId = event[0].tellerId;
		}


	}
	orgClick(num) {
		this.orgIndex = num;
		this.Display = true;
		this.orgDisplay = true;
	}
	//机构号回调
	orgValueCall(event) {
		this.Display = false;
		this.orgDisplay = false;
		if (this.orgIndex == 1) {//申请人机构
			this.postChangeBean.orgId = event[0].orgId;
			this.postChangeBean.orgName = event[0].orgName;
		} else {//申请人原机构
			this.postChangeBean.oldOrgId = event[0].orgId;
			this.postChangeBean.oldOrgName = event[0].orgName;
		}


	}
	postClick(num) {
		this.index = num;
		this.Display = true;
		this.postDisplay = true;
	}
	queryPostCall(event) {
		this.Display = false;
		this.postDisplay = false;
		if (this.index == 1) {//原岗位
			this.postChangeBean.oldPostId = event[0].postId;
			this.postChangeBean.oldPostName = event[0].postName;
		} else {//现岗位
			this.postChangeBean.postId = event[0].postId;
			this.postChangeBean.postName = event[0].postName;
		}

	}
	close() {
		this.userDisplay = false;
		this.orgDisplay = false;
		this.postDisplay = false;
	}
	//分页
	paginate(event) {
		let rows = event.rows;
		//每页显示的条数
		this.postChangeBean.pageSize = rows;
		//当前页
		let currentPage = event.page + 1;
		this.postChangeBean.pageNum = currentPage;
		this.first = event.rows * event.page;

		//调用查询的方法
		this.query();
	}
	//绑定权限id
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
}
