import { Component, OnInit } from '@angular/core';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';

@Component({
	selector: 'operation-change-record',
	templateUrl: './operationChangeRecord.component.html',
	styleUrls: ['./operationChangeRecord.component.scss'],
	providers: [Commfunc]
})
export class operationChangeRecordComponent implements OnInit {
	show: number = 1;
	constructor(
		public commfunc: Commfunc,
		public router: Router,
	) {

	}

	ngOnInit() {
        if (this.permissionCheck('SID01058_P002')) {
            this.show = 1;
        } else if (this.permissionCheck('SID01058_P001')) {
            this.router.navigate(["pages/tzb/back-manage/base-manage/operation-change-record/record-query"])
            this.show = 2;
        } 
        setTimeout(() => {
            this.judgStatu();
        })
	}
	apply(num) {
		this.show = num;
	}
	//判断当前路由 改变样式active
	judgStatu() {
		let url = window.location.href;
		let urlSplit = url.split('/');
		urlSplit.map(v => {
			if (v === 'post-change') {
				this.show = 1;
			}
			else if (v === 'record-query') {
				this.show = 2;
			}
		})
	}
	//绑定权限id
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
}
