//岗位
import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import { SelectPostBean } from './bean/select-post.bean';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { CusOperationHttpService } from '../../../../custom/http/custom-operation/custom-operation.http.service';
/**
 * 岗位选择
 */
@Component({
	selector: 'select-post1',
	templateUrl: './select-post1.component.html',
	styleUrls: ['./select-post1.component.scss'],
	providers: [CusOperationHttpService],
})
export class SelectPost1 implements OnInit {
	@Input() inValue: any;
	fileDisplay: boolean = false;
	@Output() outValue: EventEmitter<any> = new EventEmitter();
	dataList: any = [];
	selectedCities: any = [];
	checkListPro: any = [];
	selectPostBean: SelectPostBean = new SelectPostBean();
	total: number;
	//提示信息
	msgs: Message[] = [];
	first: number = 0;//分页控制
	postList: any = [];
	tabMesg: string = tableMessage;
	marketingCampaignId: string;//营销活动id
	selectedCar2:any;
	constructor(
		public commfunc: Commfunc,
		public cusOperationHttpService: CusOperationHttpService,
	) {
	}

	ngOnInit() {
		this.marketingCampaignId = this.inValue,
			this.selectPostBean.pageNum = 1;
		this.selectPostBean.pageSize = 10;
		this.first = 0;
		this.query();
	}

	toDetail(item) {
		this.fileDisplay = true;
	}
	//单选值
	checkClick2(item, event) {
		this.checkListPro = [item]
	}

	//查询
	query() {
		this.selectPostBean.pageNum = 1;
		this.selectPostBean.pageSize = 10;
		this.first = 0;
		this.queryClick();

	}
	queryClick() {
		this.cusOperationHttpService.queryPostById(this.selectPostBean).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.dataList = data.postInfoList;
				this.total = data.total;
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}
	//重置
	newQuery() {
		this.selectPostBean = new SelectPostBean();
		this.querySecleReset();
	}
	//页码重置
	querySecleReset() {
		this.selectPostBean.pageSize = 10;
		this.selectPostBean.pageNum = 1;
		this.first = 0;
		this.queryClick();
	}
	//分页
	paginate(event) {
		//每页显示的条数
		this.selectPostBean.pageSize = Number(event.rows);
		//当前页
		let currentPage = event.page + 1;
		this.selectPostBean.pageNum = currentPage;
		this.first = event.page * this.selectPostBean.pageSize;
		//调用查询的方法
		this.queryClick();
	}

	onRowSelect(event) {
		this.postList = [];
		this.postList.push(event.data)
		this.outValue.emit(this.postList);
	}
}
